/**
 * Á©∫ÊäïÊ±†ÂàóË°®ÁªÑ‰ª∂
 * ÊòæÁ§∫ÊâÄÊúâÂèØÁî®ÁöÑÂ∞èÁ∫¢Ëä±Á©∫ÊäïÊ±†
 */

import React, { useState, useEffect } from 'react';
import { AirdropPool } from '@/types/global';
import { AirdropPoolListProps } from '@/types/components';
import { secureHttpClient } from '@/lib/secureHttpClient';
import { useWallet } from '@/contexts/WalletContext';

export default function AirdropPoolList({
  pools: propPools,
  isLoading: propLoading,
  onPoolClick,
  onRefresh,
  className,
  ...props
}: AirdropPoolListProps) {
  const { wallet } = useWallet();
  const [pools, setPools] = useState<AirdropPool[]>(propPools || []);
  const [isLoading, setIsLoading] = useState<boolean>(propLoading || false);
  const [userParticipations, setUserParticipations] = useState<any[]>([]);

  // Ëé∑ÂèñÁ©∫ÊäïÊ±†ÂàóË°®
  const fetchPools = async () => {
    setIsLoading(true);
    try {
      const response = await secureHttpClient.get('/airdrop-pools');
      if (response.success && response.data) {
        setPools(response.data);
      }
    } catch (error) {
      console.error('Ëé∑ÂèñÁ©∫ÊäïÊ±†ÂàóË°®Â§±Ë¥•:', error);
    } finally {
      setIsLoading(false);
    }
  };

  // Ëé∑ÂèñÁî®Êà∑ÂèÇ‰∏éËÆ∞ÂΩï
  const fetchUserParticipations = async () => {
    if (!wallet.address) return;
    
    try {
      const response = await secureHttpClient.get(`/airdrop-pools/participate?walletAddress=${wallet.address}`);
      if (response.success && response.data) {
        setUserParticipations(response.data);
      }
    } catch (error) {
      console.error('Ëé∑ÂèñÁî®Êà∑ÂèÇ‰∏éËÆ∞ÂΩïÂ§±Ë¥•:', error);
    }
  };

  useEffect(() => {
    if (!propPools) {
      fetchPools();
    }
    if (wallet.address) {
      fetchUserParticipations();
    }
  }, [propPools, wallet.address]);

  // Âà∑Êñ∞Êï∞ÊçÆ
  const handleRefresh = async () => {
    await fetchPools();
    await fetchUserParticipations();
    if (onRefresh) {
      onRefresh();
    }
  };

  // Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶Â∑≤ÂèÇ‰∏é
  const isUserParticipated = (poolId: string): boolean => {
    return userParticipations.some(p => p.poolId === poolId);
  };

  // Ëé∑ÂèñÁî®Êà∑ÂèÇ‰∏é‰ø°ÊÅØ
  const getUserParticipation = (poolId: string) => {
    return userParticipations.find(p => p.poolId === poolId);
  };

  // Ëé∑ÂèñÊ±†Áä∂ÊÄÅ
  const getPoolStatus = (pool: AirdropPool): 'upcoming' | 'active' | 'ended' | 'full' => {
    const now = Date.now();
    
    if (now < pool.startDate) return 'upcoming';
    if (now > pool.endDate) return 'ended';
    if (pool.currentParticipants >= pool.maxParticipants) return 'full';
    return 'active';
  };

  // Ëé∑ÂèñÁä∂ÊÄÅÊ†∑Âºè
  const getStatusStyle = (status: string) => {
    switch (status) {
      case 'upcoming':
        return 'bg-blue-100 text-blue-800';
      case 'active':
        return 'bg-green-100 text-green-800';
      case 'ended':
        return 'bg-gray-100 text-gray-800';
      case 'full':
        return 'bg-red-100 text-red-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  // Ëé∑ÂèñÁä∂ÊÄÅÊñáÊú¨
  const getStatusText = (status: string) => {
    switch (status) {
      case 'upcoming':
        return 'Âç≥Â∞ÜÂºÄÂßã';
      case 'active':
        return 'ËøõË°å‰∏≠';
      case 'ended':
        return 'Â∑≤ÁªìÊùü';
      case 'full':
        return 'Â∑≤Êª°Âëò';
      default:
        return 'Êú™Áü•';
    }
  };

  // Ê†ºÂºèÂåñÊó∂Èó¥
  const formatTime = (timestamp: number): string => {
    return new Date(timestamp).toLocaleDateString('zh-CN', {
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  // ËÆ°ÁÆóËøõÂ∫¶ÁôæÂàÜÊØî
  const getProgressPercentage = (pool: AirdropPool): number => {
    return Math.min((pool.currentParticipants / pool.maxParticipants) * 100, 100);
  };

  if (isLoading) {
    return (
      <div className={`bg-white rounded-lg shadow-md p-6 ${className || ''}`} {...props}>
        <div className="text-center py-8">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500 mx-auto"></div>
          <p className="text-gray-500 mt-2">Âä†ËΩΩ‰∏≠...</p>
        </div>
      </div>
    );
  }

  return (
    <div className={`bg-white rounded-lg shadow-md ${className || ''}`} {...props}>
      {/* Â§¥ÈÉ® */}
      <div className="p-6 border-b border-gray-200">
        <div className="flex justify-between items-center">
          <h2 className="text-xl font-bold">Â∞èÁ∫¢Ëä±Á©∫ÊäïÊ±†</h2>
          <button
            onClick={handleRefresh}
            className="text-blue-500 hover:text-blue-700 text-sm"
          >
            Âà∑Êñ∞
          </button>
        </div>
        <p className="text-gray-600 mt-1">‰ΩøÁî®Â∞èÁ∫¢Ëä±ÂèÇ‰∏éÁ©∫ÊäïÔºåËé∑ÂæóSM‰ª£Â∏ÅÂ•ñÂä±</p>
      </div>

      {/* Á©∫ÊäïÊ±†ÂàóË°® */}
      <div className="p-6">
        {pools.length === 0 ? (
          <div className="text-center py-8">
            <div className="text-gray-400 mb-4">
              <svg className="w-16 h-16 mx-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v13m0-13V6a2 2 0 112 2h-2zm0 0V5.5A2.5 2.5 0 109.5 8H12zm-7 4h14M5 12a2 2 0 110-4h14a2 2 0 110 4M5 12v7a2 2 0 002 2h10a2 2 0 002-2v-7" />
              </svg>
            </div>
            <p className="text-gray-500">ÊöÇÊó†ÂèØÁî®Á©∫ÊäïÊ±†</p>
          </div>
        ) : (
          <div className="space-y-6">
            {pools.map((pool) => {
              const status = getPoolStatus(pool);
              const isParticipated = isUserParticipated(pool.id);
              const participation = getUserParticipation(pool.id);
              const progressPercentage = getProgressPercentage(pool);

              return (
                <div
                  key={pool.id}
                  className="border border-gray-200 rounded-lg p-6 hover:bg-gray-50 cursor-pointer transition-colors"
                  onClick={() => onPoolClick && onPoolClick(pool)}
                >
                  {/* Â§¥ÈÉ®‰ø°ÊÅØ */}
                  <div className="flex justify-between items-start mb-4">
                    <div className="flex-1">
                      <div className="flex items-center space-x-3 mb-2">
                        <h3 className="text-lg font-semibold">{pool.name}</h3>
                        <span className={`px-2 py-1 rounded-full text-xs ${getStatusStyle(status)}`}>
                          {getStatusText(status)}
                        </span>
                        {isParticipated && (
                          <span className="px-2 py-1 rounded-full text-xs bg-purple-100 text-purple-800">
                            Â∑≤ÂèÇ‰∏é
                          </span>
                        )}
                      </div>
                      <p className="text-gray-600 text-sm">{pool.description}</p>
                    </div>
                    <div className="text-right">
                      <div className="text-2xl font-bold text-blue-600">
                        {pool.totalReward} {pool.tokenType}
                      </div>
                      <div className="text-sm text-gray-500">ÊÄªÂ•ñÂä±</div>
                    </div>
                  </div>

                  {/* ËØ¶ÁªÜ‰ø°ÊÅØ */}
                  <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-4">
                    <div className="text-center">
                      <div className="text-lg font-semibold text-red-500">
                        üå∫ {pool.entryFee}
                      </div>
                      <div className="text-xs text-gray-500">ÂÖ•Âú∫Ë¥π</div>
                    </div>
                    <div className="text-center">
                      <div className="text-lg font-semibold">
                        {pool.currentParticipants}/{pool.maxParticipants}
                      </div>
                      <div className="text-xs text-gray-500">ÂèÇ‰∏é‰∫∫Êï∞</div>
                    </div>
                    <div className="text-center">
                      <div className="text-lg font-semibold">
                        {formatTime(pool.startDate)}
                      </div>
                      <div className="text-xs text-gray-500">ÂºÄÂßãÊó∂Èó¥</div>
                    </div>
                    <div className="text-center">
                      <div className="text-lg font-semibold">
                        {formatTime(pool.endDate)}
                      </div>
                      <div className="text-xs text-gray-500">ÁªìÊùüÊó∂Èó¥</div>
                    </div>
                  </div>

                  {/* ËøõÂ∫¶Êù° */}
                  <div className="mb-4">
                    <div className="flex justify-between text-sm text-gray-600 mb-1">
                      <span>ÂèÇ‰∏éËøõÂ∫¶</span>
                      <span>{progressPercentage.toFixed(1)}%</span>
                    </div>
                    <div className="w-full bg-gray-200 rounded-full h-2">
                      <div
                        className="bg-blue-500 h-2 rounded-full transition-all duration-300"
                        style={{ width: `${progressPercentage}%` }}
                      ></div>
                    </div>
                  </div>

                  {/* Áî®Êà∑ÂèÇ‰∏é‰ø°ÊÅØ */}
                  {isParticipated && participation && (
                    <div className="bg-purple-50 rounded-lg p-3">
                      <div className="text-sm">
                        <div className="flex justify-between mb-1">
                          <span>ÊÇ®ÁöÑÊäïÂÖ•:</span>
                          <span className="font-medium">üå∫ {participation.entryAmount}</span>
                        </div>
                        <div className="flex justify-between mb-1">
                          <span>ÂèÇ‰∏éÊó∂Èó¥:</span>
                          <span>{formatTime(participation.participatedAt)}</span>
                        </div>
                        {participation.reward && (
                          <div className="flex justify-between">
                            <span>Ëé∑ÂæóÂ•ñÂä±:</span>
                            <span className="font-medium text-green-600">
                              {participation.reward} {pool.tokenType}
                            </span>
                          </div>
                        )}
                      </div>
                    </div>
                  )}

                  {/* ÂàÜÂèëÊó∂Èó¥ */}
                  <div className="mt-4 text-center text-sm text-gray-500">
                    Â•ñÂä±ÂàÜÂèëÊó∂Èó¥: {formatTime(pool.distributionDate)}
                  </div>
                </div>
              );
            })}
          </div>
        )}
      </div>
    </div>
  );
}
